    p.mb-5(data-aos="fade-left")  Son documentos comerciales regidos por el Cdigo de Comercio y el Cdigo Civil Colombiano. En ellos se incorpora la figura del patrimonio de una persona natural o jurdica. En la realidad econmica, esta herramienta dinamiza la economa en el comercio formal e informal. Ejemplos de ttulos valores son:

