            p.mb-0 Mediante la figura del endoso, se transfiere la responsabilidad de pago de una persona a otra sin interesar quin es el que porte el documento, se agrega la expresin a la orden. Con la firma del endosante se transfiere responsabilidades al endosatario. Dos ejemplos son: la letra de cambio y el pagar.
          .tarjeta.bg-color-1.p-5(titulo="Ttulos al portador")
