            h4 Ttulos valores nominativos
            p.mb-0 Estos ttulos son expedidos a favor de una determinada persona. En el Cdigo de Comercio se determina que la persona que gira, o transfiere el ttulo debe realizar la inscripcin del tenedor en el registro que lleva el creador del ttulo (Secretara del Senado, 2020. Art. 648).
