        p.mb-5(data-aos="fade-left") Segn lo determina el Estatuto Tributario, los requerimientos exigidos para la factura son (Secretara del Senado, 2020. Art. 617):

        .row.mb-5.justify-content-center
          .col-lg-3.pe-lg-0
            figure
