          .col-lg-7.ps-lg-0
            .bg-color-23.h-100.p-3
              ol.lista-ol--cuadro
                li.bg-color-white.p-1
                  .lista-ol--cuadro__vineta
                    span a
                  | Estar denominada expresamente como factura de venta.
                li.bg-color-white.p-1
                  .lista-ol--cuadro__vineta
                    span b
                  | Apellidos y nombre o razn y NIT del vendedor o de quien presta el servicio.
                li.bg-color-white.p-1
                  .lista-ol--cuadro__vineta
                    span c
                  | *Modificado* Apellidos y nombre o razn social y NIT del adquirente de los bienes o servicios, junto con la discriminacin del IVA pagado.
                li.bg-color-white.p-1
                  .lista-ol--cuadro__vineta
                    span d
                  | Llevar un nmero que corresponda a un sistema de numeracin consecutiva de facturas de venta.
                li.bg-color-white.p-1
                  .lista-ol--cuadro__vineta
                    span e
                  | Fecha de su expedicin.
                li.bg-color-white.p-1
                  .lista-ol--cuadro__vineta
                    span f
                  | Descripcin especfica o genrica de los artculos vendidos o servicios prestados.
                li.bg-color-white.p-1
                  .lista-ol--cuadro__vineta
                    span g
                  | Valor total de la operacin.
                li.bg-color-white.p-1
                  .lista-ol--cuadro__vineta
                    span h
                  | El nombre o razn social y el NIT del impresor de la factura.
                li.bg-color-white.p-1
                  .lista-ol--cuadro__vineta
                    span i
                  | Indicar la calidad de retenedor del impuesto sobre las ventas.
                li.bg-color-white.p-1.mb-0
                  .lista-ol--cuadro__vineta
                    span j
                  | *- Declarado Inexequible Corte Constitucional-
        .row.mb-5
          .col-lg-5.order-2
            figure
              img(src="@/assets/curso/tema1/14.png", data-aos="zoom-in")
          .col-lg-7.order-1.mb-3.mb-lg-0
            p(data-aos="fade-left") Al momento de la expedicin de la factura, los requisitos de los literales a), b), d) y h), debern estar previamente impresos a travs de medios litogrficos, tipogrficos o de tcnicas industriales de carcter similar. Cuando el contribuyente utilice un sistema de facturacin por computador o mquinas registradoras, con la impresin efectuada por tales medios se entienden cumplidos los requisitos de impresin previa. El sistema de facturacin deber numerar en forma consecutiva las facturas y se debern proveer los medios necesarios para su verificacin y auditora.
        
            .bg-color-1.p-4(data-aos="fade-left")
              div.bg-color-white.mb-3
                p.mb-0 <b>PAR.</b> En el caso de las Empresas que venden tiquetes de transporte, no ser obligatorio entregar el original de la factura. Al efecto, ser suficiente entregar copia de la misma.
              div.bg-color-white
                p.mb-0 <b>PAR 2.</b> ** Adicionado- Para el caso de facturacin por mquinas registradoras, ser admisible la utilizacin de numeracin diaria o peridica, siempre y cuando corresponda a un sistema consecutivo que permita individualizar y distinguir de manera inequvoca cada operacin facturada, ya sea mediante prefijos numricos, alfabticos o alfanumricos o mecanismos similares.

