        h3.titulo-con-imagen__texto Factura electrnica
      
    p.mb-5(data-aos="fade-left")  Para la figura de facturacin electrnica debe considerarse el Estatuto Tributario (Secretara del Senado, 2020. Art. 617) y el Decreto 2242 (Sistema nico de Informacin Normativa, 2020):

    .row.mb-5
      .col-lg-4.mb-3.mb-lg-0
        figure
          img(src="@/assets/curso/tema1/15.svg", data-aos="zoom-in")
      .col-lg-8
        p(data-aos="fade-left") Segn el Decreto 2242 (Sistema nico de Informacin Normativa, 2020. Art. 2) la factura electrnica: es el documento que soporta transacciones de venta de bienes y/o servicios y que operativamente tienen lugar a travs de sistemas computacionales y/o soluciones informticas que permiten el cumplimiento de las caractersticas y condiciones que se establecen en el presente Decreto, en relacin con la expedicin, recibo, rechazo y conservacin. La expedicin de la factura electrnica comprende la generacin por el obligado a facturar y su entrega al adquirente.
    
    .row.mb-5
      .col-lg-4.mb-3.mb-lg-0.order-lg-2
        figure
          img(src="@/assets/curso/tema1/16.png", data-aos="zoom-in")
      .col-lg-8.order-lg-1
        AcordionA.mb-5(tipo="a" clase-tarjeta="tarjeta bg-color-11")
          div(titulo=" Obligado a facturar electrnicamente")
            p.mb-0 Persona natural o jurdica comprendida en el mbito de este Decreto y que como tal debe facturar electrnicamente en las condiciones que se establecen en los artculos siguientes.
          div(titulo="Adquirente")
            p.mb-0 Persona natural o jurdica que adquiere bienes y/o servicios y debe exigir factura o documento equivalente y, que, tratndose de la factura electrnica, la recibe, rechaza, cuando sea del caso, y conserva para su posterior exhibicin, en las condiciones que se establecen en el presente decreto.
          div(titulo="Proveedor tecnolgico")
            p.mb-0 Es la persona natural o jurdica que podr prestar a los obligados a facturar electrnicamente y/o a los adquirentes que opten por recibir la factura en formato electrnico de generacin, cuando unos u otros as lo autoricen, los servicios inherentes a la expedicin de la factura electrnica, incluida la entrega del ejemplar a la DIAN como se indica en el artculo 7 del presente Decreto, as como los servicios relacionados con su recibo, rechazo y conservacin. El proveedor tecnolgico deber surtir el proceso de autorizacin por parte de la DIAN previsto en el artculo 12 de este decreto.
          div(titulo="Catlogo de Participantes de Factura Electrnica")
            p.mb-0 Es el registro electrnico administrado por la DIAN, que provee informacin de los obligados a facturar electrnicamente dentro del mbito del presente Decreto, de los adquirentes que decidan recibirla electrnicamente y proveedores tecnolgicos, con el fin de facilitar su operatividad.
          div(titulo="Cdigo nico de Factura Electrnica")
            p El cdigo nico de factura electrnica para las facturas electrnicas, corresponde a un valor alfanumrico, obtenido a partir de la aplicacin de un procedimiento que utiliza datos de la factura, que adicionalmente incluye la clave de contenido tcnico de control generada y entregada por la DIAN.
            p.mb-0 El Cdigo nico de Factura Electrnica deber ser incluido como un campo ms dentro de la factura electrnica. Este cdigo deber visualizarse en la representacin grfica de las facturas electrnicas y en los cdigos bidimensionales QR definidos para tal fin.
    
    p.mb-5(data-aos="fade-left") La factura equivalente o documentos equivalentes a la factura de venta son (Angulo, 2018):

    .row.mb-5.justify-content-center
      .col-lg-3.pe-lg-0
        figure
