        p.mb-5(data-aos="fade-left") Son todos aquellos inmersos en la resolucin de la Direccin de Impuestos y Aduanas Nacionales DIAN 000042 del 2020 en el artculo 13.

        .row.mb-5.justify-content-center
          .col-lg-3.mb-3.mb-lg-0
