                p.mb-0 Otro ejemplo de documento equivalente son los expedidos por mquinas registradoras, las boletas de espectculos pblicos y todos los requisitos contemplados en el Articulo 771-2 del Estatuto Tributario (Secretara del Senado, 2020).
