    p.mb-5(data-aos="fade-left") Comnmente se conoce como orden pago, soporte que respalda los desembolsos de efectivo, por norma de control interno realizados mediante cheques. Los comprobantes de egreso se elaboran por duplicados, para anexar a la cuenta que respalda la entrega del ttulo valor, que es el cheque con que se efecta el pago.

