            p(data-aos="fade-left") Es un medio de pago convencional, donde se utiliza la figura de tarjeta habiente, utilizando un datafono para recibir pagos. Es una forma segura para no manejar efectivo, el banco o entidad financiera cobra a la empresa una comisin por el uso de este medio electrnico. Cuando se realiza la transaccin se debe guardar copia de la transaccin y anexar a la venta, como soporte.
        
